import React from 'react';
import Components from 'v02/common/generic-components/base-component';
import { connect } from 'react-redux';
import AdminCmpt from './admin';
import AuthorCmpt from './author';
import EditorCmpt from './editor';
import CustomerCmpt from './customer';
import { USER } from 'helpers/constants';

@connect(state => ({
  tenant: state.get('v02').get('common').get('tenant').get('name'),
  user: state.get('v02').get('common').get('user'),
}))
class HomeBO extends Components {

  render() {
    let HomePage = {};

    switch (this.props.user.get('id_role')) {
      case USER.ROLE.EDITOR:
        HomePage = EditorCmpt.get(this.props.tenant);
        break;
      case USER.ROLE.ADMIN:
        HomePage = AdminCmpt.get(this.props.tenant);
        break;
      case USER.ROLE.AUTHOR:
        HomePage = AuthorCmpt.get(this.props.tenant);
        break;
      case USER.ROLE.CUSTOMER:
        HomePage = CustomerCmpt.get(this.props.tenant);
        break;
      default:
        HomePage = CustomerCmpt.get(this.props.tenant);
        break;
    }
// override:
    HomePage = AdminCmpt.get(this.props.tenant);


    const props = {};

    return (
      <div className="container voffset-top-2">
       <HomePage {...props} />
      </div>
    );
  }
}

const _components = {
  default: HomeBO,
};

export default class TenantProxy {
  static get(tenant) {
    return _components[tenant] ? _components[tenant] : _components.default;
  }
}



